/* 1. 크기, 위치 */
/* 2. 레이아웃 */
/* 3. 디자인 */

.calculator {
  /* 1 */
  height: 80vh;
  width: 50vh;
  min-width: 300px;
  min-height: 500px;
  padding: 10px;

  /* 2 */
  display: flex;
  flex-direction: column;
  gap: 10px;

  /* 3 */
  background: rgba(189, 160, 160, 0.25);
  border: 3px solid white;
  border-radius: 15px;
  box-shadow: 0 0 20px black;
}

.calculator-display {
  /* 1 */
  height: 100px;
  padding: 0px 10px;

  /* 2 */
  text-align: right;
  font-size: 45px;
  line-height: 100px;

  /* 3 */
  background-color: rgb(228, 212, 212);
  border: 2px solid white;
  border-radius: 10px;
}

.calculator-buttons {
  /* 1 */
  flex: 1;
  margin: 0px;

  /* 2 */
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 5px;
}

.calculator-button {
  /* 1 */
  height: 100%;
  width: 100%;

  /* 2 */
  text-align: center;

  /* 3 */
  border: 2px solid white;
  border-radius: 15px;
  font-size: 30px;
  background-color: white;
}
.calculator-number-button {
  background-color: lightskyblue;
}

.calculator-equal-button {
  background-color: lightgreen;
}

.calculator-operation-button {
  background-color: lightsalmon;
}

.calculator-clear-button {
  background-color: lightcoral;
}
.span-two {
  grid-row: 5;
  grid-column: 3 / 5;
}